.admin-home{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.admin-sidebar{
    width: 15vw;
    height: 98vh;
    background-color: rgb(120, 127, 152);
    color: white;
    border-radius:20px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.admin-photo {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.admin-photo img{
    height: 20vh;
}

.admin-sidebar .admin-navigation{
    height: 30vh;
    display: flex;
    flex-direction: column;
}

.admin-sidebar .admin-navigation h2{
    border: 1px solid white;
    font-size: 30px;
    padding: 10px;
    border-radius: 20px;
    cursor: pointer;
    margin: 10px;
    font-weight: 700;
    transition: 0.5s;
}

.admin-sidebar .admin-navigation h2:hover{
    color: #FB236A;
    transform: scale(1.2);
    border: 1px solid #FB236A;
}


.admin-main{
    width: 80vw;
    height: 98vh;
    border-radius: 20px;
    border: 1px solid white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.admin-main h1{
    color: white;
    font-size: 70px;
}

.admin-content{
    width: 78vw;
    height: 78vh;
    background-color: rgb(42, 51, 64);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.jobSeeker{
    width: 78vw;
    height: 78vh;
    overflow: auto;
}

