*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    }

    html,body{
    width: 100%;
    height: 100%;
    background-color: #010516;
    font-family: "Imprima", sans-serif;
    font-weight: 400;
    font-style: normal;
    }

    .nav1{
        width: 30%;
    }
    .nav2{
        gap: 5%;
        width: 50%;
        height: 100%;
        display: flex;
        align-items: end;
        padding-bottom: 1%;
    }
    .nav2 a{
        color: white;
        text-decoration: none;
        font-size: larger;
    }
    .nav3{
        width: 20%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #Employers{
        color: white;
        background-color: rgba(0, 0, 0, 0);
        border-radius: 20px;
        border: 2px solid #FB236A;
        padding: 5%;
        margin-inline: 5px;
    }
    #Job-Seeker{
        color: white;
        background-color: #FB236A;
        border-radius: 20px;
        border: 2px solid #FB236A;
        padding: 5%;
        margin-inline: 5px;
    }
    .image-text h1{
        height: 15%;
        width: 36%;
        color: white;
        position: absolute;
        top: 50%;
        font-size: 70px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        /* background-color: #FB236A; */
    }
    .navimg{
        width: 100%;
        height: 70vh;
    }
    .content{
        background-color: #010516;
        margin:2% 0 2% 0 ;
        display: flex;
        margin-inline: 2%;
    }
    .box-details{
        color: white;
        font-family: "Imprima", sans-serif;
        font-weight: 400;
        font-style: normal;    background-color: #010516;
        width: 31%;
        line-height: 40px;
        padding: 20px;
    }
    .phone-numbers{
        display: flex;
    }


    iframe{
        border-radius: 20px;
    }

    #send-button{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1% 2% 1% 2%;
        margin-inline: auto;
        margin-top: 2%;
        font-family: "Imprima", sans-serif;
        font-weight: 400;
        font-style: normal;
        color: white;
        background-color: rgba(0, 0, 0, 0);
        border-radius: 20px;
        border: 2px solid #FB236A;
    }
    #send-button:hover{
        background-color: #FB236A;
        color: #F1F1F1;
    }