.homepage-container{
	background-color: #010516;
}

.homepage{
	width: 100%;
	height: 100vh;
	background:url('https://media.istockphoto.com/id/1834025548/photo/international-marketing-communication-and-online-shopping-digital-business-concept.webp?b=1&s=170667a&w=0&k=20&c=j5ynsGrZJPscpy176GVrhZ_NRsHUc-f8GXMdI4hVSYs=');
	display: flex;
	background-repeat: no-repeat;
	align-items: center;
	justify-content: center;
	background-size: 100% 100%;
}

.slogan p{
	font-size: 50px;
	color: white;
	font-weight: 700;
	text-align: center;
}


.slogan .p2{
   font-size: 30px;
}


.page2{
	height: 100vh;
	background-color: #010516;
}

.page2-heading h1{
	text-align: center;
	font-size: 30px;
	font-weight: 500;
	color: white;
	margin: 20px;
}

.homepage-industries{
	height: 250px;
	margin: 50px auto;
    position: relative;
    width: 90%;
    display: grid;
    place-items:center;
	background-color: #151138;
    overflow: hidden;
	border: 1px solid #FB236A;
	border-radius: 20px;
}

.slider-track{
    display: flex;
    width: calc(250px*10);
    animation: scroll 40s linear infinite;
  }  /* make sure to use img{width:100%} when actually using images instead of blank div  */

  @keyframes scroll{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(calc(-250px*5));
    }
  }

  .slides{
	position: relative;
    height: 250px;
    width: 250px;
    display: flex;
    align-items: center;
    padding: 30px;
  }