    .points{
        background-color: #0f1156;
        margin: 2%;
        color: white;
        border-radius: 0 70px 70px 0;
        box-shadow: 6px 5px 10px 0px #FB236A;
        font-size: 30px;
    }
    #point1{
        height: 20%;
        width: 40%;
        display: flex;
        justify-content: start;
        align-items: center;
    }
    #point2{
        height: 20%;
        width: 35%;
        display: flex;
        justify-content: start;
        align-items: center;
        /* font-size: 50px; */
    }
    #point3{
        height: 20%;
        width: 75%;
        display: flex;
        justify-content: start;
        align-items: center;
        /* font-size: 40px;    */
    }
    #point4{
        height: 20%;
        width: 37%;
        display: flex;
        justify-content: start;
        align-items: center;
        /* font-size: 40px;  */
    }
    #point5{
        height: 20%;
        width: 77%;
        display: flex;
        justify-content: start;
        align-items: center;
        /* font-size: 50px; */
    }
    #point5:hover{
        width:90%;
        transition: 0.2s ease-in-out;
    }
    #point4:hover{
        width:90%;
        transition: 0.2s ease-in-out;
    }
    #point3:hover{
        width:90%;
        transition: 0.2s ease-in-out;
    }
    #point2:hover{
        width:90%;
        transition: 0.2s ease-in-out;
    }
    #point1:hover{
        width:90%;
        transition: 0.2s ease-in-out;
    }
    .job-profile-heading{
        color: #FB236A;
        text-align: center;
        font-size: xx-large;
        background-color: #0f1156;
        margin-inline: 2%;
    }
    .paragraph-job-profiles{
        margin: 2%;
        padding: 2%;
        color: white;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-template-rows:  25% 25% 25%  ;
        background-color:#0f1156;
        grid-row-gap: 2%;
        grid-column-gap: 2%;
    }
    .profile{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 120px;
        border: 2px solid #FB236A;
        border-radius: 20px;
        transition: 0.2s ease-in-out;
    }
    .profile:hover{
        background: -webkit-linear-gradient(#FB236A, #0f1156);
    }