@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto Mono", monospace;
}

body,
html {
    width: 100%;
    height: 100%;
}

.navbar-container{
    width: 100%;
    height: 14vh;
    position: fixed;
    z-index: 99;
}

.navbar{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
    background-color: rgba(29, 28, 28, 0.557);
    backdrop-filter: 50px;
    -webkit-backdrop-filter: blur(10px);
}


.navbar img{
   transform: scale(4);
}

.navbar .navbar-navigation{
    display: flex;
    align-items: center;
    gap: 20%;
}

.navbar .navbar-navigation h2{
    font-size: 25px;
    transition: 0.5s;
    cursor: pointer;
}

.navbar .navbar-navigation h2:hover{
    color: #FB236A;
    transform: scale(1.2);
}

.navbar .navbar-authentication{
    display: flex;
    align-items: center;
    gap: 10%;
}

a{
    color: white;
    text-decoration: none;
}

.button1{
    color: white;
    background-color: rgba(0, 0, 0, 0);
    border-radius: 20px;
    border: 2px solid #FB236A;
    padding: 10%;
    margin-inline: 5px;
    transition: 0.5s;
    cursor: pointer;
    margin: 10px;
}

.button1:hover{
    background-color: #FB236A;
}

.button2{
    color: white;
    background-color: #FB236A;
    border-radius: 20px;
    border: 2px solid #FB236A;
    padding: 10%;
    margin-inline: 5px;
    transition: 0.5s;
    cursor: pointer;
    margin: 10px;
}

.button2:hover{
    background-color: rgba(0, 0, 0, 0);
}